.not-found-page-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.not-found-page-container h1 {
  font-size: 45px;
}

.not-found-page-container p {
  font-size: 30px;
}

.not-found-page-container button {
  font-family: inherit;
  padding: 1%;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  color: #435159;
  background-color: white;
}

.not-found-page-container button:hover {
  background-color: #435159;
  color: white;
  cursor: pointer;
}

.not-found-page-container button:focus {
  outline: none;
}
