@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
}
.App {
  height: 100vh;
  font-family: 'Raleway' !important;
  width: 100%;
  background-image: url(/footballData/static/media/background.afb6d9d5.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

h1,
h2,
h3,
h4 {
  font-family: 'Raleway' !important;
}

@media only screen and (max-width: 768px) {
  h1,
  h2,
  h3,
  h4 {
    text-align: center;
  }
}

.loader {
  position: fixed;
  top: 40%;
  right: 50%;
  z-index: 999999;
}

.page-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page-container .table-container {
  width: 85%;
  margin: 5%;
  height: 70%;
}
.page-container .table-container .header-row {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
.page-container .table-container .header-row input {
  padding: 1%;
  border: none;
  border-radius: 2%;
  width: 20%;
  font-family: inherit;
}
.page-container .table-container .header-row input:focus {
  outline: none;
}
.page-container .table-container .table {
  max-height: 60vh;
  overflow-y: scroll;
}
.page-container .table-container .kQMzuQ:hover {
  background-color: rgba(0, 0, 0, 0.4) !important;
  color: white !important;
  cursor: pointer;
}
.page-container .table-container .team-logo {
  width: 70px;
  height: 70px;
}
.page-container .no-results {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20%;
}
.page-container .no-results h1 {
  font-size: 45px;
}
.page-container .no-results p {
  font-size: 30px;
}
.page-container .no-results button {
  font-family: inherit;
  padding: 1%;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  color: #435159;
  background-color: white;
}
.page-container .no-results button:hover {
  background-color: #435159;
  color: white;
  cursor: pointer;
}
.page-container .no-results button:focus {
  outline: none;
}
@media (max-width: 764px) {
  .header-row {
    flex-direction: column;
    margin-bottom: 2%;
  }
  .header-row input {
    width: 70% !important;
  }
}

.page-container-single-team {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.page-container-single-team .go-back {
  width: 80%;
  margin-top: 3%;
  margin-bottom: 2%;
}

.page-container-single-team .go-back svg {
  margin-right: 1%;
}

.page-container-single-team .go-back:hover {
  cursor: pointer;
  font-weight: bold;
}

.page-container-single-team .team-container {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.page-container-single-team .team-container .header {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-bottom: 2%;
}

.page-container-single-team .team-container .header h1 {
  font-size: 3rem;
  margin-left: 5%;
}

.page-container-single-team .team-container .header .team-logo {
  max-width: 10%;
}

.page-container-single-team .team-container .inner-team-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 3%;
}
.page-container-single-team
  .team-container
  .inner-team-container
  .details-container {
  min-width: 50%;
}
.page-container-single-team
  .team-container
  .inner-team-container
  .details-container
  a {
  color: inherit;
  text-decoration: none;
}
.page-container-single-team
  .team-container
  .inner-team-container
  .details-container
  a:hover {
  cursor: pointer;
}
.page-container-single-team
  .team-container
  .inner-team-container
  .details-container
  svg {
  margin-right: 1%;
}
.page-container-single-team .team-container .inner-team-container .squad-table {
  height: 45vh;
  overflow-y: scroll;
}
.page-container-single-team
  .team-container
  .inner-team-container
  .squad-table
  .player-shirt {
  position: relative;
}
.page-container-single-team
  .team-container
  .inner-team-container
  .squad-table
  .player-shirt
  img {
  width: 60px;
}
.page-container-single-team
  .team-container
  .inner-team-container
  .squad-table
  .player-shirt
  span {
  position: absolute;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  left: 35%;
  top: 35%;
}
.page-container-single-team .no-results {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20%;
}
.page-container-single-team .no-results h1 {
  font-size: 45px;
}
.page-container-single-team .no-results p {
  font-size: 30px;
}
.page-container-single-team .no-results button {
  font-family: inherit;
  padding: 1%;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  color: #435159;
  background-color: white;
}
.page-container-single-team .no-results button:hover {
  background-color: #435159;
  color: white;
  cursor: pointer;
}
.page-container-single-team .no-results button:focus {
  outline: none;
}

@media (max-width: 764px) {
  .header {
    flex-direction: column;
    margin-top: 2%;
  }

  .header .team-logo {
    max-width: 15%;
  }

  .header h1 {
    font-size: 1.5rem !important;
  }

  .inner-team-container {
    flex-direction: column;
  }

  .inner-team-container .players-container {
    margin-bottom: 1%;
  }
}

.not-found-page-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.not-found-page-container h1 {
  font-size: 45px;
}

.not-found-page-container p {
  font-size: 30px;
}

.not-found-page-container button {
  font-family: inherit;
  padding: 1%;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  color: #435159;
  background-color: white;
}

.not-found-page-container button:hover {
  background-color: #435159;
  color: white;
  cursor: pointer;
}

.not-found-page-container button:focus {
  outline: none;
}

